// Main Settings
main {
  width: 100%;
  
  > div {
    @include verticalFlex;
    justify-content: none;
  }

  section {
    width: 80%;
    margin: 100px 0px;
  }
}

// Intro Section
section#intro {
  height: calc(100vh - 85px);
  margin: 0;

  @include spaceAround;

  h1 {
    font: bold 3em $mainFont;
  }

  picture, img {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 5px;
  }

  .introInfo {
    width: 50%;
    height: auto;

    div {
      display: flex;
      justify-content: flex-start;
    }
  }

  .imgDiv {
    width: 40%;
    height: auto;
    border: 5px solid $dark;
    border-radius: 10px;
  }

  button.primary {
    @include primaryButton;
  }
}

// Showcase Settings
section#showcase {
  position: relative;

  @include center;

  .window {
    width: 500px;
    height: 300px;
    position: relative;

    @include window;
    
    .bar {
      background: $third;

      height: 45px;
      padding: 0px 20px;
      
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
    
    .content {
      height: calc(100% - 45px);
      @include center;
    }

    &.big {
      width: 700px;
      height: 500px;
    }

    &.small1 {
      position: absolute;
      top: 65%;
      left: -15%;
      
      .bar {
        background: $fifth;
      }
      
      picture {
        width: 40%;
      }
    }
    
    &.small2 {
      box-shadow: 15px 15px black;
      position: absolute;
      top: -15%;
      left: 75%;

      .bar {
        background: $fourth;
      }

      picture {
        width: 40%;
      }
    }

    picture {
      width: 50%;

      img {
        border-radius: 20px;
      }
    }
  }

  span {
    font: 900 5em $mainFont;

    &:nth-child(odd) {
      position: absolute;
      top: 0;
      left: -10%;
    }

    &:nth-child(even) {
      text-align: right;
      position: absolute;
      right: -10%;
      bottom: 5%;
    }

    &:nth-child(2) {
      color: transparent;
      -webkit-text-stroke: 2px $dark;
      z-index: 1;
      
    }

    &:nth-child(3) {
      color: transparent;
      -webkit-text-stroke: 2px $dark;
      z-index: 1;
    }
  }

  .mouse {
    background: blue;

    width: 100px;
    height: 100px;
    border: 5px solid black;

    position: absolute;
  }
}

// About Section
section#about {
  width: 85%;
  text-align: center;

  > div {
    @include spaceAround;

    div {
      width: 400px;
      height: 400px;
      
      position: relative;
      
      @include center;

      &:nth-child(1) {
        background: $secondary;
        border: 5px solid $dark;
        border-radius: 50%;
      }
      
      &:nth-child(2) {
        background: $third;
        border: 5px solid $dark;
        border-radius: 20px;
      }
      
      &:nth-child(3) {
        background: $dark;
        clip-path: polygon(50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%);
        
        &::before {
          content: '';
          background: $fourth;
          width: 390px;
          height: 390px;

          position: absolute;
          z-index: -1;
          
          clip-path: polygon(50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%);
        }
      }
    }

    h2 {
      font-weight: normal;
      font-size: 2em;
      margin: 0px;

      span {
        font: 900 3em sans-serif;
      }
    }
  }

  h1 {
    font: normal 3em sans-serif;
    margin: 50px 0px 70px 0px;
  }
}

// Pals Section
section#pals {
  background: $dark;
  color: white;

  width: 100%;
  height: 600px;
  margin: 0;

  text-align: center;
  
  position: relative;

  @include verticalFlex;

  .msg {
    width: 400%;
    position: relative;
    left: 0;

    transition-property: left, background, color;
    transition-duration: .5s;
    transition-timing-function: ease;
    
    @include spaceEvenly;
    gap: 0;

    .palsInfo {
      width: 100vw;
      @include verticalFlex;
    }
  }

  > div {
    width: 100vw;
    height: 70%;

    @include spaceEvenly;
  }
  
  .arrowsNav {
    position: absolute;
    font-size: 3em;
    cursor: pointer;
    z-index: 1;

    &:nth-child(1) {
      left: 10%;
    }

    &:nth-child(2) {
      left: 90%;
    }
  }

  h1 {
    font: 900 3.4em $mainFont;
    color: $secondary;

    position: absolute;
    top: 10%;
  }

  h2 {
    color: $secondary;
    font: 900 2em $mainFont;
    text-transform: uppercase;

    span {
      font: normal .6em $mainFont;
    }
  }

  p {
    width: 50%;
  }

  .palsComp {
    background: $secondary;
    box-shadow: 0px 10px black;

    width: 80%;
    height: 100px;
    border: 3px solid black;
    border-radius: 20px;

    position: absolute;
    top: 90%;

    > div {
      height: 100%;
      display: flex;
      align-items: center;

      picture,
      img {
        @include center;
      }
    }
  }
}

// Services Section
section#services {
  width: 83%;

  > div {

    @include spaceAround;
  }

  .services {
    background: #ffdada;
    box-shadow: -10px 10px black;

    width: 400px;
    height: 600px;
    padding: 20px;
    margin: 10px 0px;
    border: 3px solid black;
    border-radius: 20px;

    @include verticalFlex;

    button {
      border-radius: 20px;
      width: 90%;
    }
  }

  .services:nth-child(2) {
    .primary {
      background: $third;
    }
  }

  .services:nth-child(3) {
    .primary {
      background: $fourth;
    }
  }

  h1 {
    font: 900 3em sans-serif;
    text-align: center;
    margin-bottom: 50px;
  }

  .primary {
    @include primaryButton;
  }
}

// Reach Settings
section#reach {
  height: 900px;
  position: relative;

  @include spaceAround;

  .ceo {
    background: white;

    width: 500px;
    height: 150px;
    margin-bottom: 200px;
    border: 5px solid black;
    border-radius: 20px;
    transform: perspective(10px) rotateX(1deg);
    align-self: flex-end;

    .ceoName {
      background: $third;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      transform: perspective(none) rotate3d(none);

      position: absolute;
    }
  }

  .earth {
    background: $third;

    width: 400px;
    height: 400px;
    border: 10px solid black;
    border-radius: 50%;

    position: relative;
    align-self: flex-start;

    @include center;

    picture, img {
      @include center;
    }
    
    .txt {
      position: absolute;
      width: 100%;
      height: 100%;
      
      animation: rot-txt 10s linear infinite;
    }

    .txt span {
      text-align: center;
      font-size: 3em;
      font-weight: 900;

      position: absolute;
      top: -15%;
      left: 50%;
      transform-origin: 0 250px;
    }

    @keyframes rot-txt {
      0% {
        transform: rotate(0);
      }

      100% {
        transform: rotate(360deg);
      }
    }
  }
}

// Improve Settings
section#improve {
  background: $third;

  width: 100%;
  height: 1100px;
  margin: 0px;

  position: relative;

  @include center;

  h1,
  h2 {
    font: 900 3em $mainFont;
    text-align: center;
  }

  > div {
    width: 70%;
    height: 60%;

    @include grid(3, 2);

    div {
      box-shadow: -10px 10px $dark;

      padding: 20px;
      border: 4px solid $dark;
      border-radius: 20px;
      margin: 20px;
    }

    > div:nth-child(1) {
      background: $fifth;
      grid-row: 1 / span 2;

      @include verticalFlex;
      
      h1 {
        font-size: 4em;
        transform: rotate(-90deg);
        width: 100%;
      }
    }

    > div:nth-child(2) {
      background: $primary;
      grid-column: 2 / span 3;

      @include verticalFlex;

      button {
        width: 100%;
      }
    }

    > div:nth-child(3) {
      background: $fourth;
    }

    > div:nth-child(4) {
      background: $fifth;

      @include grid(2, 2);
    }
  }

  .waves {
    position: absolute;
    top: -19.5%;
  }

  .primary {
    @include primaryButton(true);
  }
}

// Media Settings
.servDiv {
  width: 100%;
  height: 100%;
  border: none;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;

  pointer-events: none;
}